/* =========================================
   Estilos base (mobile-first)
   ========================================= */
   body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    line-height: 1.6;
  }
  
  img {
    max-width: 100%;
    height: auto;
    display: block;
  }
  
  .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 1rem;
  }
  
  .grid-container {
    display: grid;
    gap: 1rem; /* Propiedad moderna que necesita prefijos en navegadores muy antiguos */
  }
  
  /* Ajustes globales para enlaces de navegación */
  .navbar ul {
    display: flex;
    gap: 1rem;
  }
  
  /* Enlace "Saltar navegación" (oculto por defecto, visible al enfocar) */
  .skip-link {
    position: absolute;
    top: -40px;
    left: 0;
    background: #333;
    color: #fff;
    padding: 8px;
    z-index: 100;
  }
  .skip-link:focus {
    top: 0;
  }
  
  /* Header */
  header {
    background: #333;
    color: #fff;
    padding: 1rem 0;
    text-align: center;
  }
  header h1 {
    margin: 0;
  }
  nav ul {
    list-style: none;
    padding: 0;
    display: flex;
    justify-content: center;
    gap: 1rem;
  }
  nav ul li a {
    color: #fff;
    text-decoration: none;
  }
  
  /* Main content */
  main {
    padding: 20px;
    max-width: 800px;
    margin: 0 auto;
  }
  
  /* Footer */
  footer {
    background: #333;
    color: #fff;
    text-align: center;
    padding: 10px 0;
  }
  footer nav ul {
    display: flex;
    justify-content: center;
    gap: 1rem;
  }
  footer nav ul li a {
    color: #fff;
    text-decoration: none;
  }
  
  /* Estilos para las tarjetas */
  .grid-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 20px;
    padding: 20px;
  }
  .card {
    border: 1px solid #ddd;
    border-radius: 8px;
    overflow: hidden;
    text-align: center;
    background: #fff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  .card:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  }
  .card img {
    width: 100%;
    height: 200px;
    object-fit: cover;
  }
  .card h3 {
    margin: 15px 0 10px;
    font-size: 1.5rem;
  }
  .card p {
    padding: 0 15px;
    color: #555;
  }
  .card .btn {
    display: inline-block;
    margin: 15px;
    padding: 10px 20px;
    background: #007BFF;
    color: #fff;
    text-decoration: none;
    border-radius: 5px;
    transition: background 0.3s ease;
  }
  .card .btn:hover {
    background: #0056b3;
  }
  
  /* Estilos para la página de detalle */
  .featured-image {
    width: 100%;
    height: auto;
    border-radius: 8px;
    margin-bottom: 20px;
  }
  .gallery {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 10px;
    margin-bottom: 20px;
  }
  .gallery img {
    width: 100%;
    height: auto;
    border-radius: 8px;
  }
  .video-container {
    position: relative;
    padding-bottom: 56.25%; /* Relación de aspecto 16:9 */
    height: 0;
    overflow: hidden;
    margin-bottom: 20px;
  }
  .video-container iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 8px;
  }
  
  /* Estilos para la página de enlaces */
  main ul {
    list-style-type: none;
    padding: 0;
  }
  main ul li {
    margin: 10px 0;
  }
  main ul li a {
    color: #007BFF;
    text-decoration: none;
    transition: color 0.3s ease;
  }
  main ul li a:hover {
    color: #0056b3;
  }
  main section {
    margin-bottom: 30px;
  }
  main h3 {
    margin-bottom: 15px;
    font-size: 1.5rem;
    color: #333;
  }
  
  /* Cards (otra sección similar) */
  .cards {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 1rem;
  }
  
  /* =========================
     BREAKPOINTS
     ========================= */
  
  /* ========== MOBILE MUY PEQUEÑO (por debajo de 576px) ========== */
  @media (max-width: 576px) {
    html {
      font-size: 14px;
    }
    .navbar ul {
      flex-direction: column;
      gap: 0.5rem;
    }
    /* Mínimos retoques si quieres forzar 1 columna en la galería o tarjetas */
    .grid-container,
    .cards,
    .gallery {
      grid-template-columns: 1fr;
    }
  }
  
  /* ========== TABLET / MÓVILES GRANDES (577px a 768px) ========== */
  @media (min-width: 577px) and (max-width: 768px) {
    html {
      font-size: 15px;
    }
    /* 2 columnas en grids si hay suficiente espacio */
    .grid-container,
    .cards {
      grid-template-columns: repeat(2, 1fr);
    }
    .gallery {
      grid-template-columns: repeat(2, 1fr);
    }
    /* Navbar puede ponerse horizontal si quieres */
    .navbar ul {
      flex-direction: row;
      justify-content: space-around;
    }
  }
  
  /* ========== TABLET / DESKTOP PEQUEÑO (769px a 992px) ========== */
  @media (min-width: 769px) and (max-width: 992px) {
    html {
      font-size: 16px;
    }
    .container {
      max-width: 700px;
    }
    /* Aumentamos columnas a 3 si cabe */
    .grid-container,
    .cards {
      grid-template-columns: repeat(3, 1fr);
    }
    .gallery {
      grid-template-columns: repeat(3, 1fr);
    }
  }
  
  /* ========== DESKTOP MEDIANO (993px a 1200px) ========== */
  @media (min-width: 993px) and (max-width: 1200px) {
    html {
      font-size: 17px;
    }
    .container {
      max-width: 960px;
    }
    /* 3-4 columnas, según tu preferencia */
    .grid-container,
    .cards {
      grid-template-columns: repeat(4, 1fr);
    }
    .gallery {
      grid-template-columns: repeat(4, 1fr);
    }
  }
  
  /* ========== DESKTOP GRANDE (por encima de 1200px) ========== */
  @media (min-width: 1201px) {
    html {
      font-size: 18px;
    }
    .container {
      max-width: 1200px;
    }
    /* Layout amplio, 4 columnas o más */
    .grid-container,
    .cards {
      grid-template-columns: repeat(4, 1fr);
    }
    .gallery {
      grid-template-columns: repeat(4, 1fr);
    }
  }
  
  /* Ejemplo de ocultar Navbar en móvil (<=600px) 
     si quieres implementar un menú hamburguesa */
  @media (max-width: 600px) {
    .navbar ul {
      display: none;
    }
  }
  